.holiday {
	position: relative;
	display: block;
	&__img {
		font-size: 0;
		overflow: hidden;
		img {
			width: 100%;
			object-fit: cover;
			height: 520px;
			display: block;
			transition: transform 1.4s;
			@include hover {
				transform: scale(1.3);
				transform-origin: center; } } }
	&__wrap {
		position: relative;
		margin: -32px 16px 0;
		padding: 16px 20px 35px;
		z-index: 1;
		text-align: center;
		background-color: $white; }
	&__title {
		transition: color .3s ease-in-out;
		@include hover {
			color: $lightBrown; } }
	&-slider {
		.description {
			margin-bottom: 44px; }
		@include sm {
			margin-bottom: 20px; } } }
