.breadcrumbs {
	margin: 24px 0 32px;
	letter-spacing: -0.03em;
	@include md {
		font-size: 14px; }
	@include sm {
		margin: 16x 0 24px;
		font-size: 12px; }
	&__item {
		position: relative;
		vertical-align: top;
		display: inline-block;
		color: $gray;
		user-select: none;
		&:not(:first-child) {
			padding-left: 21px;
			@include md {
				padding-left: 18px; }
			@include sm {
				padding-left: 12px; }
			&:before {
				position: absolute;
				content: "/";
				top: 0;
				left: 6px;
				font-size: 16px;
				color: $silver;
				@include rotate(10);
				@include md {
					left: 5px; }
				@include sm {
					left: 2px;
					font-size: 13px; } } }
		a {
			position: relative;
			color: $black;
			transition: color .3s;
			display: block;
			@include hover {
				color: $lightBrown;
				.icon-chevron-left {
					color: $lightBrown; } } }
		.icon-chevron-left {
			margin: 4px 8px 0 0;
			font-size: 9px;
			color: $silver;
			transition: color .3s;
			vertical-align: top;
			display: inline-block; }
		.icon-chevron-down {
			margin-left: 10px;
			margin-top: 6px;
			font-size: 7px;
			color: $silver;
			transition: color .3s, transform .3s;
			vertical-align: top;
			display: inline-block; }
		&.is-open {
			.icon-chevron-down {
				transform: rotate(180deg); } }
		&.has-list {
			cursor: pointer; } }

	&__drop {
		opacity: 0;
		visibility: hidden;
		position: absolute;
		top: calc(100% + 8px);
		left: -15px;
		background-color: $white;
		width: 225px;
		transition: opacity .3s, visibility .3s;
		box-shadow: 0 6px 12px rgba(#000, .16);
		.breadcrumbs__item.is-open & {
			opacity: 1;
			visibility: visible;
			z-index: 2; } }
	&__sublist {
		display: flex;
		flex-direction: column; }
	.breadcrumbs__sublist {
		a {
			padding: 9px 0 8px 16px;
			&:hover {
				color: initial; } }
		li {
			transition: background-color .3s;
			&:hover {
				background-color: $gallery; } } } }
