.collaborator {
	position: relative;
	text-align: left;
	vertical-align: top;
	display: inline-flex;
	align-items: center;
	font-size: 13px;
	line-height: 1.4;
	&__img {
		width: 64px;
		height: 64px;
		border-radius: 50%;
		background-position: 50% 50%;
		background-size: cover; }
	&__wrap {
		padding-left: 16px;
		overflow: hidden; }
	&__name {
		margin: 0 0 8px;
		color: $black;
		font-family: $montserrat;
		@include f(16px, 500, 1.25); }
	&__img {
		flex-shrink: 0; } }
